<template>
	<div class="content">
		<!-- title -->
		<div class="top_title"></div>
		<div
			style="margin: auto;margin-left: 45px;margin-right: 45px;"
			class="acceleerate_con"
		>
			<!--  -->
			<div class="seach">
				<div class="seach_top">
					<el-input
						placeholder=""
						v-model="input"
						class="input-with-select"
						maxlength="70"
						@keyup.enter.native="onSubmit"
					>
						<i
							slot="prefix"
							class="el-input__icon el-icon-search"
							@click="seachuser()"
						></i>
					</el-input>
					<div
						class="seach_top_right"
						@click="option_display()"
						:class="[rotate ? 'sethui' : 'setwhile']"
					>
						
						<i
							class="el-icon-caret-bottom"
							:class="[
								rotate
									? 'fa fa-arrow-down go'
									: 'fa fa-arrow-down aa',
							]"
						></i>
					</div>
				</div>
				<div v-if="optiondisplay" class="seach_bottom">
					<span></span>
					<el-select
						v-model="value"
						placeholder=""
						@change="getdata()"
					>
						<el-option label="" value></el-option>
						<el-option
							v-for="(item, index) in options"
							:key="index + 'reat'"
							:label="item.label"
							:value="item.value"
						></el-option>
					</el-select>
					<span style="margin-left: 10px;"></span>
					<el-date-picker
						v-model="value1"
						type="datetimerange"
						range-separator=""
						start-placeholder=""
						end-placeholder=""
						@change="gettimes"
						:picker-options="pickerOptions0"
					></el-date-picker>
					<!-- <el-button
						type="primary"
						size="mini"
						@click="seachuser()"
						style="margin-left: 10px;"
						></el-button
					> -->
					<el-button
						plain
						size="mini"
						@click="reset()"
						style="margin-left: 10px;"
						></el-button
					>
				</div>
			</div>
			<!--  -->
			<div class="con_lable">
				<div
					class="con_lable_top_btn"
					style="padding:10px 0 18px;display: flex;justify-content: space-between;"
				>
					<div>
						<el-button type="primary" @click="new_btn">
							<span class="el-icon-plus"></span>
							
						</el-button>
					</div>
				</div>

				<!--  -->
				<el-table
					ref="multipleTable"
					:data="tableData"
					stripe
					border
					tooltip-effect="dark"
					style="width: 100%"
					:cell-style="rowClass"
					:header-cell-style="headClass"
					:default-sort="{ prop: 'date', order: 'descending' }"
					@selection-change="handleSelectionChange"
					@sort-change="tableSortChange"
				>
					<el-table-column
						type="selection"
						width="55"
					></el-table-column>
					<el-table-column
						prop="dominds"
						label=""
					></el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<span v-if="scope.row.status == 0"></span>
							<span
								v-else-if="scope.row.status == 1"
								style="color: red;"
								></span
							>
							<span
								v-else-if="scope.row.status == 2"
								style="color: red;"
								></span
							>
						</template>
					</el-table-column>
					<el-table-column
						prop="time_create"
						sortable="custom"
						label=""
					>
						<template slot-scope="scope"
							><span>{{
								scope.row.time_create | settimes
							}}</span></template
						>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-button
								@click="revise(scope.$index, scope.row)"
								class="lab_btn"
								type="text"
								size="small"
								></el-button
							>
							<el-button
								v-if="scope.row.status == 0"
								@click="disableuser(scope.$index, scope.row)"
								type="text"
								size="small"
								></el-button
							>
							<el-button
								v-if="scope.row.status != 0"
								@click="enableuser(scope.$index, scope.row)"
								type="text"
								size="small"
								></el-button
							>
							<el-button
								v-if="scope.row.status != 0"
								@click="deleateuser(scope.$index, scope.row)"
								class="lab_btn"
								type="text"
								size="small"
								></el-button
							>
						</template>
					</el-table-column>
				</el-table>
				<!--  -->
				<div
					style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;"
				>
					<div>
						<el-button
							type="text"
							size="small"
							@click="enableuser()"
							></el-button
						>
						<el-button
							type="text"
							size="small"
							@click="disableuser()"
							></el-button
						>
						<el-button
							type="text"
							size="small"
							@click="deleateuser()"
							></el-button
						>
					</div>
					<fenye
						style="padding: 10px 0 20px;;text-align:right"
						@fatherMethod="getpage"
						@fathernum="gettol"
						:pagesa="total_cnt"
						:currentPage="currentPage"
					></fenye>
				</div>

				<!--  -->
				<el-dialog
					:title="dialog_title"
					:visible.sync="dialogFormVisible"
					class="add_dialog"
					@close="handleClose"
				>
					<el-form :model="form" ref="accelerate_dialog">
						<el-form-item
							label=""
							:label-width="formLabelWidth"
							prop="name"
							:rules="[{ validator: jiourl, trigger: 'blur' }]"
						>
							<el-input
								v-model="form.name"
								autocomplete="off"
								placeholder="http://https://72"
							></el-input>
						</el-form-item>
					</el-form>
					<div slot="footer" class="dialog-footer">
						<el-button @click="quxzteaone"> </el-button>
						<el-button type="primary" @click="quxzteao">{{
							dialog_title == '' ? '' : ''
						}}</el-button>
					</div>
				</el-dialog>
			</div>
		</div>
	</div>
</template>

<script>
import fenye from '@/components/fenye';
import { dateToMs, getymdtime } from '../../servers/sevdate';
import {
	query_url,
	change_state,
	delete_url,
	getterminal,
} from '../../servers/api';
export default {
	data() {
		return {
			rotate: false,
			optiondisplay: false,
			currentPage: 1,
			currentPage: 1,
			dialog_title: '',
			title_num: 0,
			total_cnt: 1,
			order: 0,
			input: '', //
			value: '',
			value1: '',
			options: [
				{
					value: 0,
					label: '',
				},
				{
					value: 1,
					label: '',
				},
				{
					value: 2,
					label: '',
				},
			],
			pickerOptions0: {
				shortcuts: [
					{
						text: '',
						onClick(picker) {
							const end = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							const start =
								new Date(
									new Date(
										new Date().toLocaleDateString()
									).getTime()
								) -
								3600 * 1000 * 24 * 1;
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							start.setTime(
								start.getTime() - 3600 * 1000 * 24 * 6
							);
							picker.$emit('pick', [start, end]);
						},
					},
					{
						text: '',
						onClick(picker) {
							const end = new Date();
							const start = new Date(
								new Date(
									new Date().toLocaleDateString()
								).getTime()
							);
							start.setTime(
								start.getTime() - 3600 * 1000 * 24 * 29
							);
							picker.$emit('pick', [start, end]);
						},
					},
				],
				disabledDate(time) {
					return time.getTime() > Date.now();
				},
			},
			tableData: [
				{
					dominds: 'http://www.baidu.com',
					status: '1',
					time_create: '1583906243',
				},
			],
			form: {
				name: '',
			},
			formLabelWidth: '120px',
			dialogFormVisible: false,
			currentSelection: [],
			multipleSelection: [],
		};
	},
	filters: {
		settimes(data) {
			if (data) {
				var stat = getymdtime(data);
				return stat;
			} else {
				return data;
			}
		},
	},
	components: {
		fenye,
	},
	mounted() {},
	methods: {
		//
		getuserlist() {
			console.log('');
		},
		//
		tem(tenum) {
			let parmas = new Object();
			parmas.type = tenum;
			if (tenum) {
				parmas.url = tenum.dominds;
			} else {
				parmas.url = multipleSelection;
			}
			getterminal(parmas)
				.then((res) => {
					if (res.status == 0) {
					}
				})
				.catch((error) => {
					console.log(error);
				});
		},
		//
		revise(num, row) {
			console.log(row, num);
			this.dialog_title = '';
			this.form.name = row.dominds;
			this.title_num = num;
			this.dialogFormVisible = true;
		},
		//
		disableuser(num, row) {
			console.log(row);
			this.tem(0);
		},
		//
		enableuser(num, row) {
			console.log(row);
			this.tem(1);
		},
		//
		deleateuser(num, row) {
			console.log(row);
			this.tem(2);
		},
		//URL
		new_btn() {
			this.dialog_title = '';
			this.dialogFormVisible = true;
		},
		//
		seachuser() {
			this.getuserlist();
		},
		//
		reset() {
			this.value = '';
			this.value1 = '';
			this.input = '';
			this.getuserlist();
		},
		//
		gettimes() {
			this.getuserlist();
		},
		//
		option_display() {
			this.optiondisplay = !this.optiondisplay;
			this.rotate = !this.rotate;
		},
		//
		getpage(pages) {
			this.tolpage = pages;
			this.getuserlist();
		},
		//
		gettol(pagetol) {
			this.pagesize = pagetol;
			this.getuserlist();
		},
		//
		onSubmit() {
			this.getuserlist();
		},
		getdata() {
			this.getuserlist();
		},
		//
		tableSortChange(column) {
			this.currentPage = 1;
			if (column.order == 'descending') {
				this.order = 1;
			} else {
				this.order = 0;
			}
			this.getuserlist();
		},
		//
		handleSelectionChange(val) {
			if (val.length) {
				this.currentSelection = val.map((item) => item.dominds);
			}
			console.log(this.currentSelection);
		},
		// 
		updateSelection() {
			if (this.currentSelection.length) {
				this.multipleSelection = this.multipleSelection.concat(
					this.currentSelection
				);
				this.currentSelection = [];
				console.log('updateSelection:', this.multipleSelection);
			}
		},
		// 
		formatChoosen(arr) {
			const urlArr = this.tableData.map((item) => item.dominds);
			urlArr.forEach((item, index) => {
				if (this.multipleSelection.includes(item)) {
					// itemselectioncurrentSelection
					const idx = this.multipleSelection.indexOf(item);
					this.multipleSelection.splice(idx, 1);
					setTimeout(() => {
						this.$refs.multipleTable.toggleRowSelection(
							this.tableData[index]
						);
					}, 200);
				}
			});
			console.log(
				'formatChoosen--multipleSelection:',
				this.multipleSelection
			);
		},
		getBLen(str) {
			if (str == null) return 0;
			if (typeof str != 'string') {
				str += '';
			}
			return str.replace(/[^\x00-\xff]/g, '01').length;
		},
		//url
		jiourl(rule, value, callback) {
			if (value === '') {
				callback(new Error(''));
			} else {
				var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
				// objExp = new RegExp(resyzm);
				if (this.getBLen(value) > 72) {
					callback(new Error('1024'));
				} else if (resyzm.test(value) === false) {
					callback(new Error(''));
				} else {
					callback();
				}
			}
		},
		//--
		handleClose() {
			this.quxzteaone();
		},
		//--
		quxzteaone() {
			this.dialogFormVisible = false;
			this.$refs.accelerate_dialog.resetFields();
			this.form.name = '';
		},
		//--
		quxzteao() {
			let _this = this;
			this.$refs.accelerate_dialog.validate((valid) => {
				if (valid) {
					if (this.dialog_title == '') {
						let num = _this.title_num;
						this.$confirm(
							'' +
								_this.form.name +
								'' +
								_this.tableData[num].dominds +
								'' +
								_this.form.name,
							'',
							{
								distinguishCancelAndClose: true,
								confirmButtonText: '',
								cancelButtonText: '',
							}
						)
							.then(() => {
								_this.tableData[num].dominds = _this.form.name;
								this.dialogFormVisible = false;
								this.$message({
									type: 'info',
									message: '',
								});
							})
							.catch((action) => {
								this.$message({
									type: 'info',
									message:
										action === 'cancel'
											? ''
											: '',
								});
								this.dialogFormVisible = false;
							});
					} else {
						let timestamp = new Date().getTime();
						let obj = {};
						obj.dominds = this.form.name;
						obj.status = '';
						obj.time_create = timestamp / 1000;
						this.tableData.push(obj);
						this.$message({
							message: '',
							type: 'success',
						});
					}
				}
			});
		},
		// 
		headClass() {
			return 'text-align: center;background:#F3F6FB;color:#333333;font-size:16px;';
		},
		// 
		rowClass() {
			return 'text-align: center;';
		},
	},
};
</script>

<style lang="scss" scoped>
.content {
	width: 100%;
	height: 100%;
	text-align: left;
	.seach {
		width: 100%;
		margin: 30px 0 30px 0;
		background: #ffffff;
		border-radius: 2px;
		padding: 21px 37px;
		box-shadow: 0px 0px 7px 0px rgba(41, 108, 171, 0.1);
		.seach_top {
			width: 100%;
			height: 60px;
			line-height: 60px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			.input-with-select {
				width: 15%;
			}
			.seach_top_right {
				width: 80px;
				text-align: center;
				height: 36px;
				line-height: 36px;
				margin-left: 10px;
			}
		}
		.seach_bottom {
			height: 72px;
			background: rgba(242, 246, 250, 1);
			border-radius: 2px;
			display: flex;
			align-items: center;
			padding-left: 27px;
		}
	}
	.con_lable {
		width: 100%;
		background: #ffffff;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 2px 3px 0px rgba(6, 17, 36, 0.14);
		border-radius: 2px;
		padding: 8px 37px 0;
	}
}
.el-dialog {
	margin-top: 25vh;
	width: 25%;
	height: 280px;
}
.lab_btn {
	padding: 0;
}
</style>



